<template>
  <div v-if="memberInfor.kefuMobile" class="side">
    <ul>
      <li class="first">
        <a href="#">
          <div class="sidebox">
            <img class="icon" src="../../assets/images/icon/qrcode.png">
            <div class="qrcode">
              <img class="qrcode-img" :src="memberInfor.qrcode" alt="">
              <!-- <img class="qrcode-img" src="../../assets/images/icon/qr.jpeg" alt=""> -->
            </div>
          </div>
        </a>
      </li>
      <li>
        <a href="#">
          <div class="sidebox">
            <img class="icon"  src="../../assets/images/icon/wechat.png">
            微信: {{memberInfor.wx}}
          </div>
        </a>
      </li>
      <li>
        <a href="javascript:void(0);">
          <div class="sidebox">
            <img class="icon" src="../../assets/images/icon/QQ.png">
            QQ客服: {{memberInfor.qq}}
          </div>
        </a>
      </li>
      <li>
        <a href="javascript:void(0);">
          <div class="sidebox">
            <img class="icon" src="../../assets/images/icon/mobile.png">
            手机号: {{memberInfor.kefuMobile}}
          </div>
        </a>
      </li>
      <li style="border:none;">
        <a @click="goTop" class="sidetop">
          <img class="icon" src="../../assets/images/icon/side_icon05.png">
        </a>
      </li>
    </ul>
  </div>
</template>

<script>
import $ from "jquery";
export default {
  name: "kefu",
  data() {
    return {
      memberInfor: {
        qrcode: "",
        kefuMobile: "",
        wx: ""
      }
    };
  },
  created() {
    const timer = setInterval(() => {
      this.memberInfor =
        JSON.parse(sessionStorage.getItem("memberInfor")) || {};
      console.log(this.memberInfor);
      if (this.memberInfor.mobile) clearInterval(timer);
    }, 1000);
  },
  mounted() {
    $(document).ready(function() {
      $(".side ul li").hover(
        function() {
          var width = '200px';
          
          if ($(this).hasClass('first')) width = '54px'
          $(this).find(".sidebox")
            .stop()
            .animate({ width }, 200)
            .css({
              opacity: "1",
              filter: "Alpha(opacity=100)",
              background: "#ae1c1c"
            });
        },
        function() {
          $(this)
            .find(".sidebox")
            .stop()
            .animate({ width: "54px" }, 200)
            .css({
              opacity: "0.8",
              filter: "Alpha(opacity=80)",
              background: "#000"
            });
        }
      );
    });
  },
  methods: {
    goTop() {
      var pp = document.documentElement;
      var stop = document.body;
      pp.scrollTop = 0
      // var timer = setInterval(function() {
      //   //获取滚动条的滚动高度
      //   var osTop = pp.scrollTop || stop.scrollTop;
      //   //用于设置速度差，产生缓动的效果
      //   var speed = Math.floor(-osTop / 6);
      //   pp.scrollTop = stop.scrollTop = osTop + speed;
      //   isTop = true; //用于阻止滚动事件清除定时器
      //   if (osTop == 0) {
      //     clearInterval(timer);
      //   }
      // }, 30);
    }
  }
};
</script>

<style scoped>
@charset "utf-8";
* {
  margin: 0;
  padding: 0;
  list-style-type: none;
}
a,
img {
  border: 0;
}
body {
  font: 12px/180% Arial, Helvetica, sans-serif, "新宋体";
}
/* side */
.side {
  position: fixed;
  width: 54px;
  height: 275px;
  right: 0;
  top: 214px;
  z-index: 100;
}
.side ul li {
  width: 54px;
  height: 54px;
  float: left;
  position: relative;
  border-bottom: 1px solid #444;
}
.side ul li .sidebox {
  position: absolute;
  width: 54px;
  height: 54px;
  top: 0;
  right: 0;
  transition: all 0.3s;
  background: #000;
  opacity: 0.8;
  filter: Alpha(opacity=80);
  color: #fff;
  font: 14px/54px "微软雅黑";
}
.side ul li .sidetop {
  width: 54px;
  height: 54px;
  line-height: 54px;
  display: inline-block;
  background: #000;
  opacity: 0.8;
  filter: Alpha(opacity=80);
  transition: all 0.3s;
}
.side ul li .sidetop:hover {
  background: #ae1c1c;
  opacity: 1;
  filter: Alpha(opacity=100);
}
.side ul li img.icon {
  float: left;
  width: 54px;
}
.qrcode{
  width: 150px;
  height: 150px;
  background: #000;
  padding: 10px;
  box-sizing: border-box;
  position: absolute;
  left: -150px;
  display: none;
  
}
.side ul li.first:hover .qrcode{
  display: block
}
.qrcode .qrcode-img {
  width: 130px;
  height: 130px;
}
</style>




